import React from "react";
import ReactDOM from "react-dom";

import { SplitColorChannelText } from "react-text-fun";

import "./styles.css";

function App() {
  const [value, setValue] = React.useState(0);

  const updateShader = (e) => setValue(e.clientX);

  return (
    <div className="App" onMouseMove={updateShader}>
      <SplitColorChannelText
        text="Happy Mother's Day Momma!!"
        fontSize={100}
        rotation={0.8}
        rgbOffset={value === 0 ? 0.095 : value / 10000}
        addBlur={false}
        addNoise={true}
      />
      <p1>
        <SplitColorChannelText
          text="from the Kusek Family"
          fontSize={75}
          rotation={0.8}
          rgbOffset={value === 0 ? 0.095 : value / 10000}
          addBlur={false}
          addNoise={true}
        />
      </p1>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
